/* eslint-disable consistent-return */
// 
import { format } from 'date-fns';
import { message } from 'antd';

//  
const shortenDescription = (text, maxLength) => {
  if (typeof text !== 'string') {
    return '';
  }
  if (text.length <= maxLength) {
    return text;
  }
  return `${text.substring(0, maxLength)}...`;
};

const formattedDate = (date) =>
  date ? format(new Date(date), 'MMM dd, yyyy') : '';

function validateAndTrimData(data) {
  const trimmedData = {
    ...data,
    title: data.title.trim(),
    description: data.description.trim(),
    body: data.body.trim(),
    tags: data.tags.map((tag) => (typeof tag === 'string' ? tag.trim() : tag)),
  };

  if (
    !/^(?![\s\n]*$).+/.test(trimmedData.title) ||
    !/^(?![\s\n]*$).+/.test(trimmedData.body) ||
    !/^(?![\s\n]*$).+/.test(trimmedData.description)
  ) {
    message.error('Data cannot be empty or contain only spaces');
    return;
  }

  return trimmedData;
}
//  
const validationMethods = {
  validateUsername: (value) => {
    if (!value) {
      return '  ';
    }
    if (value.length < 3 || value.length > 20) {
      return '     3  20 ';
    }
    return true;
  },
  validateEmail: (value) => {
    if (!value) {
      return '  ';
    }
    if (!/^\S+@\S+\.\S+$/.test(value)) {
      return '   ';
    }
    return true;
  },
  validatePassword: (value) => {
    if (!value) {
      return ' ';
    }
    if (value.length < 6 || value.length > 40) {
      return '    6  40 ';
    }
    return true;
  },
  validateRepeatPassword: (value, getValues) => {
    const password = getValues('password');
    if (!value) {
      return '  ';
    }
    if (value !== password) {
      return '  ';
    }
    return true;
  },
  validateAvatar: (value) => {
    if (value && !/^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/.test(value)) {
      return ' URL ';
    }
    return true;
  },

  validateCheckbox: (value) => value === true, // ,     true ()
  validateTitle: (value) => {
    if (!value) {
      return ' ';
    }
    return true;
  },
  validateDescription: (value) => {
    if (!value) {
      return '  ';
    }
    return true;
  },
  validateBody: (value) => {
    if (!value) {
      return ' ';
    }
    return true;
  },
};

export {
  shortenDescription,
  formattedDate,
  validateAndTrimData,
  validationMethods,
};
